<template>
  <div class="Sign">
      <img src="../../../assets/login/bg.png">
      <div class="welcome">欢迎参加{{drillName}},请点击下方的按钮进行签到</div>
      <div class="btn">
          <i id="signId" class="el-icon-date"></i>
          <button v-if="sign=='点击签到'"  @click="signIn()">{{sign}}</button>
          <button id="signed" v-if="sign=='已签到'" @click="showBox()">{{sign}}</button>
      </div>

      <div class="model" v-show="isShow">
          <div class="box">
              <div class="boxHeader">
                  <span>抽签结果</span>
                  <i class="el-icon-close" @click="boxHide"></i>
              </div>
              <div class="boxIpt">
                  <div class="boxRest" v-if="groupState!=='此演练不分组'">
                      <p>你被分配在{{group}}</p>
                      <p id="sameGroup">同组其他成员有：</p>
                      <p v-for="(item,keys) in drawResult" v-if="userId!=item.id" :key=keys>{{item.section.section_one}}{{item.section.section_two}} -- {{item.name}}</p>
                  </div>
                  <div class="boxRest" v-if="groupState==='此演练不分组'">
                      <p>此次演练为一人一组</p>
                  </div>
              </div>
              <div class="modelBtn">
                  <button @click="determine()">确定</button>
              </div>
          </div>
      </div>
  </div>
</template>
<script>
  export default {
      data(){
          return{
              drillName:'',//演练名称
              sign:'点击签到',
              isShow:false,//显示抽签结果弹框
              isShowBox:false,//导演端是否已经抽签
              isSign:false,//用户是否已经签到
              drawResult:[
                  {
                      section:{
                          section_one:'',
                          section_two:'',
                      }
                  }
              ],//抽签结果
              group:'',//分组结果
              restState:'',
              groupState:'',//是否分组
              designId:'',//演练id
              role:'',//演练角色
              time1:'',
          }
      },
      methods: {
//          获取此演练的名字
          getDrillName(){
              this.$axios.post('/api/design-name',{
	              "design_id":this.designId
              }).then((res)=>{
//                  console.log(res)
                  this.drillName=res.data.data.name;
              }).catch((error)=>{
              	console.log(error)
              })
          },

//        人员签到
          signIn(){
              this.$axios.post('/api/sign-in',{
                "id":this.userId
              }).then((res)=> {
    //              console.log(res)
                  if(res.data.message==="成功") {
                      this.sign = '已签到';
                      this.isSign=true;
                      document.getElementById('signId').className = 'el-icon-emergency-qiandao';
                  }
                }).catch((error)=> {
                  console.log(error)
                })
          },

//        判断是否签到
          getIsSign(){
              this.$axios.post('/api/user-sign',{
                  "design_id":this.designId
              }).then((res)=>{
                  //              console.log(res)
                  for(var i in res.data.data){
                      if(res.data.data[i].user_id===this.userId){
                          if(res.data.data[i].user_status===1){
                              this.isSign=true;
                              this.sign='已签到';
                              document.getElementById('signId').className = 'el-icon-emergency-qiandao';
                          }
                      }
                  }
                  if(res.data.data.score_user!=''){
                      if(res.data.data.score_user[0].user_id == this.userId){
                          if(res.data.data.score_user[0].user_status == 1){
                              this.isSign=true;
                              this.sign='已签到';
                              document.getElementById('signId').className = 'el-icon-emergency-qiandao';
                          }
                      }
                  }
              }).catch((error)=>{
                  console.log(error);
              })

          },

//        如果已经签到，并且导演端已经分组，显示弹框
          showBox(){
              if(this.isShowBox==true&&this.role=='参演'){
                  this.isShow=true;
              }
          },

//      关闭抽签结果弹框
          boxHide() {
              this.isShow = !this.isShow;
          },

//        抽签结果确定
          determine(){
              this.isShow = !this.isShow;
          },

//        显示分组
          showGroup(){
              this.$axios.post('/api/the-draw',{
                "design_id":this.designId
              }).then((res)=> {
    //              console.log(res)
                  if(res.data.message!='暂无数据'&&this.role=='参演'){
                      if(res.data.data[0].name==='此演练不分组'){//不分组
                          this.groupState="此演练不分组";
                          this.isShow=true;
                          this.isShowBox=true;
                      }else{
                          for(var i in res.data.data){
                              if(res.data.data[i].user === []){ //还没分组

                              }else{//分组展示
                                  for(var j in res.data.data[i].user){
                                  	    console.log(res.data.data)
                                      if(res.data.data[i].user[j].id===this.userId){//该组里的某人的id与登录的用户id相同，就知道该人分配在哪一组
//                                          console.log(res.data.data[i].user.length)
                                          this.group=res.data.data[i].name;//用户的组名
                                          this.drawResult=res.data.data[i].user;//该组成员
                                      }
	                                  if(res.data.data[i].user.length==1){//该组只有一个人，则不显示"同组其他成员有"字样
		                                  document.getElementById('sameGroup').style.display="none";
	                                  }
                                      this.isShow=true;
                                      this.isShowBox=true;
                                  }
                              }
                          }
                      }
                  }
              }).catch((error)=> {
                  console.log(error)
              })
          },

//        导演端是否开始演练
          getIsStart(){
              this.$axios.post('/api/look-status',{
                "design_id":this.designId
              }).then((res)=>{
    //            console.log(res)
                  if(res.data.data.design_location!==null){
                      this.$router.push('/practiceStart');
                      clearInterval(this.time1)
                  }
              }).catch((error)=>{
                  console.log(error)
              })
          },
      },

      created(){
          this.$store.state.showHeader = '3';
          this.userId= JSON.parse(localStorage.user).id;
          this.designId=localStorage['drill_project'];
          this.role=localStorage['roles'];
          this.getIsSign();
      },
      mounted(){
          this.getDrillName();
          clearInterval(this.time1);
          this.time1 = setInterval(()=>{
              if(this.isShowBox==false&&this.isSign){
                  this.showGroup();
              }
              this.getIsStart();
          },2000)
      }
  }
</script>
<style lang="less" rel="stylesheet/less">
    @import '../../../../static/css/base.css';
    .Sign{
        min-width: 1000px;
        width: 100%;
        /*margin-bottom: -50px;*/
        >img{
            width: 100%;
            height: auto;
            display: block;
        }
        .welcome{
            font-size: 20px;
            width: 1000px;
            height: 50px;
            margin:0 auto;
            position: absolute;
            top:20%;
            left: 0;
            right:0;
            text-align: center;
        }

        .btn{
            display: block;
            width: 170px;
            height: 38px;
            border-radius: 21.5px;
            background-color: #4a90e2;
            line-height: 38px;
            margin: 48px 0 0 57px;
            cursor: pointer;
            position: absolute;
            left: 50%;
            top: 20%;
            margin-left: -85px;
            i{
                margin-left:40px;
                font-size: 15px;
                color: white;
            }
            button{
                cursor: pointer;
                // margin: 0 45px;
                font-size: 15px;
                background-color: #4a90e2;
                color:white;
                outline: none;
                border: none;
            }
        }
        .model {
            position: fixed;
            z-index: 99;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.699);
            display: table;
            .box {
                background-color: white;
                width: 422.4px;
                height: 373px;
                margin: 0 auto;
                position: relative;
                top: 180px;
                .boxHeader {
                    height: 50px;
                    background-color: #4a90e2;
                    text-align: center;
                    span {
                        line-height: 50px;
                        font-size: 22px;
                        letter-spacing: -0.3px;
                        color: #ffffff;
                    }
                    i {
                        color: white;
                        font-size: 25px;
                        font-weight: 500;
                        position: relative;
                        left:130px;
                        top: 3px;
                        cursor: pointer;
                    }
                }

                .boxIpt {
                    margin: 25px 0 ;
                    .boxRest{
                        width:375px;
                        height:200px;
                        margin-left:40px;
                        p{
                            margin-bottom: 15px;
                            line-height: 18px;
                            font-size: 16px;
                            color: #4a4a4a;
                            text-align: left;
                        }
                    }
                }
                .modelBtn {
                    text-align: center;
                    margin-top: 30px;
                    button {
                        position: relative;
                        left: 10px;
                        width: 231.6px;
                        height: 40px;
                        border-radius: 3px;
                        background-color: #50b4ec;
                        color: white;
                        cursor: pointer;
                        outline: none;
                        font-size: 16px;
                        letter-spacing: 4px;
                    }
                }
            }
        }
  }
</style>



